import React from "react"

class Answer extends React.Component {
    state = {
        disabled: false
    }

    handleOnClick = (questionId, answer) => {
        this.setState({
            disabled:true
        })
        this.props.handleUserAnswer(questionId, answer)
        this.props.updateAnswers(answer)

    }
    render() {

        if (this.props.answer){
            return (
                <button
                    className="answerBtn"
                    disabled = {this.state.disabled}
                    value={this.props.answer} onClick={()=>this.handleOnClick(this.props.questionId, this.props.answer)}
                        >{this.props.answer}
                        </button>
            )
        } else {
            return null;
        }
    }
}

export default Answer