import React from "react"
import Answer from "./Answer"

class AnswerList extends React.Component {
    state = {
        answers: this.props.answers
    }

    updateAnswers = (text) => {
        let updatedAnswers = []
        let prevState = [...this.state.answers]

        prevState.forEach(answer => {
            if (answer === text) {
                updatedAnswers.push(text)
            }
        })

        console.log(updatedAnswers)

        this.setState({
            answers:updatedAnswers
        })
    }

    render() {
        
        const answerElements = this.state.answers.map((answer, index) => {
            return (
                <Answer 
                answer={answer} 
                key = {index} 
                questionId = {this.props.questionId} 
                handleUserAnswer={this.props.handleUserAnswer} 
                updateAnswers = {this.updateAnswers} />
            )
        })

        return (
            <div className="answer-list">{answerElements}</div>
        )
    }
}

export default AnswerList