import React from "react"
import AnswerList from "./AnswerList"
import Question from "./Question"

class QuestionAnswerContainer extends React.Component {
    render() {

        return (
            <div className="questionBox">
                <Question question={this.props.info.question} />
                <AnswerList answers={this.props.info.answers} questionId={this.props.info.questionId} handleUserAnswer={this.props.handleUserAnswer} />
            </div>
        )

    }
}

export default QuestionAnswerContainer