import React from "react"
import Header from "./Header"
import QuestionAnswerContainer from "./QuestionAnswerContainer"
import Result from "./Result"
import QuizService from "../QuizService"

class QuizContainer extends React.Component {
    constructor(props) {
        super(props);
       
        this.state = {
            numAnswered:0,
            score:0,
            questionBank:[]
        }

    }
    getQuestions = () => {
      QuizService().then(question => {
        this.setState({
          questionBank:question
        })
      })
  }
    componentDidMount() {
      this.getQuestions()
    }

    handleUserAnswer = (questionId, answer) => {

      // update the score based on user response
      //TODO: to be improved  - don't want to iterate through the array everytime the user responds
      this.state.questionBank.forEach(item => {
        if (item.questionId===questionId && item.correct === answer) {
          this.setState(prevState => ({
            score:prevState.score+1

          }))
        }
      })
    
      this.setState(prevState =>({
        numAnswered:prevState.numAnswered+1

      }))

    }

    playAgain = () => {
      this.getQuestions()
      this.setState({
        numAnswered:0,
        score:0
      })
    }


    render() {

        const allQandAItems = []
        this.state.questionBank.forEach(item => {
            allQandAItems.push(<QuestionAnswerContainer handleUserAnswer={this.handleUserAnswer} info={item}  key={item.questionId}/>)
        })

        if (this.state.numAnswered < 5 && allQandAItems) {
          return (
            <div className="">
                <Header/>
                <div>{allQandAItems}</div>
            </div>
        )

        } else {
          return (
            <Result score = {this.state.score} playAgain = {this.playAgain}/>
          )
        }

        
    }
}


export default QuizContainer